.wpte-product-general-style-2{
    .wpte-general-layout-image-area{
        position: relative;
        overflow: hidden;
    }
    .wpte-general-layout-product-thumb{
        line-height: 1;
    }
    .wpte-general-layout-product-cart-button-wrapper{
        position: absolute;
        bottom: -50px;
        width: 100%;
        transition: all 0.3s;
        a{
            width: 100%;
            text-align: center;
            border: 0;
            border-radius: 0;
            font-size: 16px;
            padding: 5px 0px 5px 0px;
            .wpte-icon{
                margin-right: 10px;
            }
            span{
                font-family: Arial;
                font-size: 16px;
                text-transform: none;
                line-height: 1.8em;
            }
        }
    }
    .wpte-general-layout-cart-button{
        span,
        a,
        .button{
            display: block;
        }

        a,
        .button{
            color: #ffffff;
            background: #000;
            &:hover{
                background: rgb(254, 113, 97);
            }
        }
        a {
            .wpte-cart-text,
            .wpte-variable-text,
            .wpte-groupde-text,
            .wpte-external-text,
            .wpte-view-cart-text{
                display: inline-block;
            }
        }
    }

    .wpte-general-layout-product-img{
        img{
            transition: transform .5s; /*Animation*/
        }
    }

}

.wpte-product-general-style-2:hover{
    .wpte-general-layout-product-cart-button-wrapper{
        bottom: 0;
    }
    .wpte-general-layout-product-img{
        img{
            transform: scale(1.2);
        }
    }
}

/* Badge */
.wpte-general-layout-image-area{
    ul{
        z-index: 1;
    }
}
